.teammate {
    display: flex;
    align-items: center;
    width: 340px;
    height: 70px;
    font-size: 24px;
    border-radius: 10px;
    margin: 0 auto 20px;
    padding-left: 10px;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.team-item {
    display: flex;
    align-items: center;
}

.team-img {
    margin-right: 20px;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}